<template>
    <div class="fixed inset-0 flex items-center justify-center z-[9999] py-5">
        <div class="absolute inset-0 bg-black opacity-30 pointer-events-none"></div>
        <!-- Container modal -->
        <div class="relative z-10 p-4 bg-white rounded-xl lg:min-w-[350px] lg:max-w-[450px] min-w-[340px] max-w-[350px]">
            <form class="p-2 text-gray-700 w-full h-full text-sm">
                <h3 class="text-center font-bold text-lg">Crear cuenta master</h3>
                <ItemForm @value-input="saveNombreComercial" label="Nombre comercial" name="nombre_comercial"
                    id="nombre_comercial" placeholder="Ingrese el nombre comercial de la empresa" :value="nombrecomercial" />
                <ItemForm @value-input="saveRazonSocial" label="Razón social" name="empresa" id="empresa"
                    placeholder="Ingrese el nombre de la empresa" :value="razonsocial" />
                <ItemForm @value-input="updateusuario" label="Usuario" name="usuario" id="usuario"
                    placeholder="Ingrese un usuario" :value="usuario" />
                <PasswordField @update-paswoord="updatepassword" texto="Contraseña" placeholder="Ingrese contraseña" :valor="password"/>
                <!-- Dropdown para la sección Contacto -->
                <div class="dropdown">
                    <button
                        class="dropdown-toggle bg-orange-400 flex justify-between items-center w-full rounded-lg text-white px-5 py-2 mt-2"
                        @click.prevent="toggleContacto" :class="{ 'bg-orange-500': mostrarContacto }">
                        <span>Detalles empresa</span>
                        <i class="fa-solid fa-angle-down"></i>
                    </button>
                    <div class="dropdown-menu mx-2 px-2 py-4 rounded-md bg-slate-200 transition-max-height duration-300"
                        :class="{
                            'max-h-[500px]': mostrarContacto,
                            'max-h-0': !mostrarContacto,
                            'overflow-hidden': !mostrarContacto,
                            'pt-0 pb-0': !mostrarContacto
                        }">
                        <ItemForm @value-input="saveRuc" label="RUC" name="ruc" placeholder="Ruc de la empresa" :value="ruc" />
                        <ItemForm @value-input="saveDireccion" label="Dirección" name="direccion" placeholder="Dirección de la empresa" :value="direccion" />
                        <ItemForm @value-input="saveTelefono" label="Teléfono" name="telefono"
                            placeholder="Número de contacto" :value="telefono" />
                    </div>
                </div>
            </form>
            <div class="btns flex gap-2 justify-center items-center pt-2">
                <button @click="closeModal"
                    class="bg-gray-400 hover:bg-gray-500 text-white text-sm p-2 px-4 rounded-xl transition duration-300">Cerrar</button>
                <button @click="saveDatos"
                    class="bg-blue-400 hover:bg-blue-500 text-white text-sm p-2 px-4 rounded-xl transition duration-300">Actualizar</button>
            </div>
        </div>
        <!-- Container modal -->
    </div>
</template>
  
<script>
import { ref, onMounted } from 'vue';
import PasswordField from '@/components/generales/PasswordField.vue';
import ItemForm from '../generales/ItemForm.vue';
import {buscarCuentaMaestra} from '@/services/CuentaMaestraService';
import md5 from 'md5';
export default {
    name: 'ModalEditar',
    components: { PasswordField, ItemForm },
    emits: ['cancelar-editar', 'aceptar-editar'],
    props:{
        idCuentaMaestra: String
    },
    setup(props, { emit }) {
        const usuario = ref('');
        const razonsocial = ref('');
        const nombrecomercial = ref('');
        const password = ref('');
        const direccion = ref('');
        const telefono = ref('');
        const ruc = ref('');
        const id_usuario = ref('');
        const id_cuentamaestra = ref('');
        const passwordNuevo = ref('');
        const msgValidacion = ref('');
        const mostrarUbicacion = ref(false);
        const mostrarContacto = ref(false);

        onMounted(()=> {
            buscarCuentaMaestra(props.idCuentaMaestra)
            .then((response)=> {
                usuario.value = response.data.cuentasmaestras[0].usuario;
                razonsocial.value = response.data.cuentasmaestras[0].empresa;
                nombrecomercial.value = response.data.cuentasmaestras[0].nombre;
                password.value = response.data.cuentasmaestras[0].clave;
                direccion.value = response.data.cuentasmaestras[0].direccion;
                telefono.value = response.data.cuentasmaestras[0].contacto;
                ruc.value = response.data.cuentasmaestras[0].ruc;
                id_cuentamaestra.value = response.data.cuentasmaestras[0].id_cuentamaestra;
                id_usuario.value = response.data.cuentasmaestras[0].id;
            })
        })

        const toggleUbicacion = () => {
            mostrarUbicacion.value = !mostrarUbicacion.value;
        };

        const toggleContacto = () => {
            mostrarContacto.value = !mostrarContacto.value;
        };

        const updateusuario = (value) => {
            usuario.value = value;
        };

        const saveRazonSocial = (value) => {
            razonsocial.value = value;
        };

        const updatepassword = (value) => {
            passwordNuevo.value = value;
        };
        const saveNombreComercial = (value) => {
            nombrecomercial.value = value;
        }
        const saveDireccion = (value) => {
            direccion.value = value;
        };

        const saveRuc = (value) => {
            ruc.value = value;
        };
        const saveTelefono = (value) => {
            telefono.value = value;
        };

        const closeModal = () => {
            emit('cancelar-editar');
        };

        const saveDatos = () => {
            let contraseñaFinal = password.value;
            if (passwordNuevo.value !== "") {
                contraseñaFinal = md5(passwordNuevo.value);
            }
            emit('aceptar-editar', {
                /* datos del formulario */
                usuario: usuario.value,
                razonsocial: razonsocial.value,
                password: contraseñaFinal,
                nombrecomercial: nombrecomercial.value,
                ruc: ruc.value,
                telefono: telefono.value,
                direccion: direccion.value,
                id_cuentamaestra: id_cuentamaestra.value,
                id_usuario: id_usuario.value
            });
        };
        return {
            usuario,
            razonsocial,
            nombrecomercial,
            password,
            direccion,
            telefono,
            ruc,
            msgValidacion,
            mostrarUbicacion,
            mostrarContacto,
            toggleUbicacion,
            toggleContacto,
            updateusuario,
            saveRazonSocial,
            updatepassword,
            saveDireccion,
            saveRuc,
            saveNombreComercial,
            saveTelefono,
            closeModal,
            saveDatos
        };
    },
};
</script>

