import axios from 'axios';

const baseURL = process.env.VUE_APP_BASE_URL;

export function listarCuentasMaestras() {
    return axios.get(`${baseURL}cuentasmaestras/list`)
}

export function buscarCuentaMaestra(idCuentaMaestra){
    return axios.get(`${baseURL}cuentasmaestras/search?idCuentaMaestra=${idCuentaMaestra}`)
}

export function editarCuentaMaestra(codempresa, tipousuario, data){
    return axios.put(`${baseURL}cuentasmaestras/edit`,{
        codempresa_root: codempresa,
        tipousuario_root: tipousuario,
        nombre_comercial: data.nombrecomercial,
        razon_social: data.razonsocial,
        usuario: data.usuario,
        clave: data.password,
        direccion: data.direccion,
        ruc: data.ruc,
        contacto: data.telefono, 
        id_usuario: data.id_usuario,
        id_cuentamaestra: data.id_cuentamaestra
    })
}

export function crearCuentasMaestras(codempresa, tipousuario, data){
    return axios.post(`${baseURL}cuentasmaestras/create`,{
        codempresa_root: codempresa,
        tipousuario_root: tipousuario,
        nombre_comercial: data.nombrecomercial,
        razon_social: data.razonsocial,
        usuario: data.usuario,
        clave: data.password,
        direccion: data.direccion,
        ruc: data.ruc,
        contacto: data.telefono,
    })
}

export function eliminarCuentasMaestras(idUsuario, idCuentaMaestra,tipousuario ) {
    return axios.delete(`${baseURL}cuentasmaestras/delete?idUsuario=${idUsuario}&idCuentaMaestra=${idCuentaMaestra}&tipousuario=${tipousuario}`)
}