<template>
    <table class="w-full text-xs text-left text-gray-500 dark:text-gray-400">
        <thead class="text-xs uppercase bg-gray-400 text-white">
            <tr>
                <th scope="col" class="px-6 py-3">
                    Usuario
                </th>
                <th scope="col" class="px-6 py-3">
                    Empresa
                </th>
                <th scope="col" class="px-6 py-3">
                    Codigo empresa
                </th>
                <th scope="col" class="px-6 py-3">
                    Dirección
                </th>
                <th scope="col" class="px-6 py-3">
                    Ruc
                </th>
                <th scope="col" class="px-6 py-3">
                    Telefono
                </th>
                <th scope="col" class="px-6 py-3">
                    Acciones
                </th>
            </tr>
        </thead>
        <tbody>
            <tr v-if="listaCuentasMaestras.length === 0">
                <td class="border px-4 py-4 text-xs text-center" colspan="8">No se encontró ningún dato
                    para esta tabla</td>
            </tr>
            <tr v-else class="bg-white border-b hover:bg-gray-100 ease-in duration-300"
                v-for="cuentamaster in listaCuentasMaestras" :key="cuentamaster.id">
                <th scope="row" class="flex items-center px-6 py-3 text-gray-900 whitespace-nowrap ">
                    <img class="w-10 h-10 rounded-full" src="../../assets/icon-user.png" alt="Icon user">
                    <div class="pl-3">
                        <div class="text-gray-800 font-medium">{{ cuentamaster.usuario }}</div>
                    </div>
                </th>
                <td class="px-6 py-3 text-gray-800">
                    {{ cuentamaster.nombre }}
                </td>
                <td class="px-6 py-3 text-gray-800">
                    {{ cuentamaster.codempresa }}
                </td>
                <td class="px-6 py-3 text-gray-800">
                    {{ cuentamaster.direccion }}
                </td>
                <td class="px-6 py-3 text-gray-800">
                    {{ cuentamaster.ruc }}
                </td>
                <td class="px-6 py-3 text-gray-800">
                    {{ cuentamaster.contacto }}
                </td>
                <td class="px-6 py-3">
                    <a href="#"
                        class="mr-2 p-2 font-medium text-white  bg-green-500 hover:bg-green-700 ease-in duration-300 rounded-lg" @click.prevent="editarCuentaMaster(cuentamaster.id, cuentamaster.id_cuentamaestra)">Editar</a>
                    <a href="#"
                        class="p-2 font-medium text-white  bg-red-500 hover:bg-red-700 ease-in duration-300 rounded-lg" @click.prevent="eliminarCuentaMaster(cuentamaster.id, cuentamaster.id_cuentamaestra)">Eliminar</a>
                </td>
            </tr>
        </tbody>
    </table>
</template>
<script>
export default ({
    name: "TableCuentasMaestras",
    props: {
        listaCuentasMaestras: Array
    },
    methods: {
        eliminarCuentaMaster(idUsuario, idCuentaMaster){
            this.$emit('eliminar-cuentamaster', idUsuario, idCuentaMaster)
        },
        editarCuentaMaster(idUsuario, idCuentaMaster){
            this.$emit('editar-cuentamaster', idUsuario, idCuentaMaster)
        }
    }
})
</script>