<template>
    <section class="min-h-full w-full pb-[5rem] lg:pb-[2rem]">
        <div class="mb-8 flex justify-between items-center">
            <h3 class="title-vista text-2xl font-semibold text-neutral-600">Administrar cuentas</h3>
            <button @click.prevent="openModal"
                class="py-2 px-3 rounded-xl bg-orange-400 text-white text-sm hover:bg-orange-500 transition duration-300">
                Crear cuenta
            </button>
        </div>
        <div class="text-sm overflow-x-auto shadow-md sm:rounded-2xl">
            <TableCuentasMaestras :listaCuentasMaestras="listaCuentasMaestras" @eliminar-cuentamaster="eliminarCuentaMaster"
                @editar-cuentamaster="editarUsuario" />
        </div>
        <!-- Modal -->
        <transition name="modal" mode="out-in">
            <ModalCreate v-show="openModalV" @close-modal="closeModal" @save-datos="crearCuentaMaster" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModalDelete v-if="confirmDelete" @aceptar-eliminar="aceptarEliminar" @cancelar-eliminar="cancelarEliminar"
                pregunta="¿Estás seguro de eliminar a esta cuenta master?" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModalEditar v-if="openModalEdit" @aceptar-editar="aceptarEditar" @cancelar-editar="cancelarEditar"
                :idCuentaMaestra="idCuentaMaster" :idEmpresa="idEmpresaSet" />
        </transition>
        <transition name="modal" mode="out-in">
            <ModalError v-if="openModalError" title="Advertencia" mensaje="Se debe rellenar todos los campos"
                @close-modal="closeModalError" />
        </transition>
        <transition name="modal" mode="out-in">
            <ConfirmEdit v-if="openSuccessEdit" title="Logrado" mensaje="Se ha editado correctamente"
                @close-modal="closeSuccessEdit" />
        </transition>
    </section>
</template>
  
<script>
import { ref, onMounted } from 'vue';
import store from '@/store';
import TableCuentasMaestras from '@/components/cuentasmaestras/TableCuentasMaestras.vue';
import ModalCreate from '@/components/cuentasmaestras/ModalCreate.vue';
import ModalDelete from '@/components/generales/ModalDelete.vue';
import ModalError from '../components/generales/ModalError.vue';
import ConfirmEdit from '../components/generales/ConfirmEdit.vue';
import ModalEditar from '@/components/cuentasmaestras/ModalEditar.vue';
import { listarCuentasMaestras, crearCuentasMaestras, eliminarCuentasMaestras,editarCuentaMaestra } from '@/services/CuentaMaestraService';


export default {
    name: 'AdminCuentasMaestras',
    components: {
        TableCuentasMaestras,
        ModalCreate,
        ModalDelete,
        ModalEditar,
        ModalError,
        ConfirmEdit
    },
    setup() {
        const listaCuentasMaestras = ref([]);
        const openModalV = ref(false);
        const confirmDelete = ref(false);
        const openModalError = ref(false);
        const openModalEdit = ref(false);
        const idUsuarioSet = ref("");
        const idCuentaMaster = ref("");
        const openSuccessEdit = ref(false);
        onMounted(() => {
            listarCuentasMaestras()
                .then(response => {
                    listaCuentasMaestras.value = response.data.cuentasmaestras;
                });
        });
        const openModal = () => {
            openModalV.value = true;
        };
        const closeModal = () => {
            openModalV.value = false;
        };
        const closeModalError = () => {
            openModalError.value = false;
        };
        const closeSuccessEdit = () => {
            openSuccessEdit.value = false;
            window.location.reload();
        }
        const crearCuentaMaster = (dataformulario) => {
            if (dataformulario.usuario == '' || dataformulario.razonsocial == '' || dataformulario.password == '' ||
                dataformulario.direccion == '' || dataformulario.nombrecomercial == '' || dataformulario.telefono == '' ||
                dataformulario.ruc == '') {
                openModalV.value = false;
                openModalError.value = true;
            } else {
                crearCuentasMaestras(store.state.codempresa, store.state.tipousuario, dataformulario)
                    .then(() => {
                        openModalV.value = false;
                        window.location.reload();
                    })
                    .catch((error) => {
                        console.log(error)
                    })
            }
        };
        const eliminarCuentaMaster = (idUsuario, idCuentamaster) => {
            idUsuarioSet.value = idUsuario;
            idCuentaMaster.value = idCuentamaster;
            confirmDelete.value = true;
        }
        const aceptarEliminar = () => {
            eliminarCuentasMaestras(idUsuarioSet.value, idCuentaMaster.value, store.state.tipousuario)
                .then(response => {
                    console.log(response);
                    window.location.reload();
                    confirmDelete.value = false;
                    idUsuarioSet.value = ""
                    idCuentaMaster.value = ""
                });
        }
        const cancelarEliminar = () => {
            confirmDelete.value = false;
            idUsuarioSet.value = ""
            idCuentaMaster.value = ""
        }
        const editarUsuario = (idUsuario, idEmpresa) => {
            idUsuarioSet.value = idUsuario;
            idCuentaMaster.value = idEmpresa;
            openModalEdit.value = true;
        }
        const cancelarEditar = () => {
            openModalEdit.value = false;
        }

        const aceptarEditar = (dataformulario) => {
            if (dataformulario.usuario == '' || dataformulario.razonsocial == '' || dataformulario.password == '' ||
                dataformulario.direccion == '' || dataformulario.nombrecomercial == '' || dataformulario.telefono == '' ||
                dataformulario.ruc == '') {
                openModalEdit.value = false;
                openModalError.value = true;
            } else {
                editarCuentaMaestra(store.state.codempresa, store.state.tipousuario, dataformulario)
                    .then(response => {
                        console.log(response)
                        openSuccessEdit.value = true;
                        openModalEdit.value = false;
                    });
            }
        }
        return {
            listaCuentasMaestras, openModalV,
            openModal, closeModal,
            crearCuentaMaster, confirmDelete,
            eliminarCuentaMaster, aceptarEliminar,
            cancelarEliminar, closeModalError,
            cancelarEditar, openModalError,
            openModalEdit, editarUsuario,
            idUsuarioSet, idCuentaMaster,
            aceptarEditar, openSuccessEdit,
            closeSuccessEdit
        };
    },
};
</script>
<style scoped>
.modal-enter-active,
.modal-leave-active {
    transition: opacity 0.3s;
}

.modal-enter-from,
.modal-leave-to {
    opacity: 0;
}
</style>


